.gradient {
    background: #000000;
    background: linear-gradient(to bottom, #000, #353f7f);
    height: calc(100vh - 478px);
}

.black {
    background: #000000;
}

img {
    display: block;
    margin: auto;
}

body {
    margin: 0;
    padding: 0;
}

html,
body {
    background: #000000;
    height: 95%;
}

h1 {
    font-size: 25px;
    text-align: center;
    font-weight: normal;
    font-family: "Catamaran", sans-serif;
    color: #999999;
}

.main {
    margin-bottom: 35px;
}

@media (max-width: 500px) {
    h1 {
        font-size: 26px;
        text-align: center;
        font-weight: normal;
        font-family: "Catamaran", sans-serif;
        color: #999999;
    }
    img {
        display: none;
    }
    .gradient {
        background: #000000;
        background: linear-gradient(to bottom, #000, #353f7f);
        height: calc(84vh - 50px);
    }
}

a:-webkit-any-link {
    text-decoration: none;
    color: #ba55d3;
    cursor: default;
}

.link {
    text-decoration: none;
    color: #ba55d3;
    cursor: default;
}